import { Link } from 'react-router-dom';
import { Product } from '../../types/Product';
import { ProductActions } from '../ProductActions';
import { FeatureList } from '../FeatureList';
import {
  generateSlugForProduct,
} from '../../helpers/utils';

import './ProductCard.scss';

type Props = {
  product: Product;
};

export const ProductCard: React.FC<Props> = ({ product }) => {
  const {
    imageUrl,
    name,
    price,
    discount,
    id,
    type,
    screen,
    capacity,
    ram,
  } = product;

  const features = {
    screen: screen ? `${screen.split(' ')[0]}”` : '',
    capacity: capacity ? `${capacity.slice(0, -2)} ${capacity.slice(-2)}` : '',
    ram: ram ? `${ram.slice(0, -2)} ${ram.slice(-2)}` : '',
  };
  const slug = generateSlugForProduct(type, id);
  const priceAfterDiscount = price * ((100 - discount) / 100);

  const handleClick = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  return (
    <div className="ProductCard">
      <div className="ProductCard__img-container">
        <Link to={slug} onClick={handleClick}>
          <img
            src={imageUrl}
            alt={name}
            className="ProductCard__img"
          />
        </Link>
      </div>

      <Link to={slug} className="ProductCard__name">
        {name}
      </Link>

      <div className="ProductCard__price">
        {`$${priceAfterDiscount}`}
        {discount !== 0 && (
          <div className="ProductCard__initial-price">
            {`$${price}`}
          </div>
        )}
      </div>

      <div className="ProductCard__features">
        <FeatureList features={features} />
      </div>

      <div className="ProductCard__actions">
        <ProductActions product={product} />
      </div>
    </div>
  );
};
