import { useEffect, useState } from 'react';
import { fetchProducts } from '../../api';
import { Product } from '../../types/Product';
import { filterProducts } from '../../helpers/utils';
import { ProductCard } from '../ProductCard';

import './ProductsSlider.scss';

enum Direction {
  PREV = 'prev',
  NEXT = 'next',
}

type Props = {
  title: string;
  filter: string;
  sortBy: string;
};

const sliderSettings = {
  containerWidth: 1040,
  cardWidth: 272,
  cardGap: 16,
  cardsPerPage: 4,
};

export const ProductsSlider: React.FC<Props> = ({
  title,
  filter,
  sortBy,
}) => {
  const {
    cardWidth,
    cardGap,
    cardsPerPage,
  } = sliderSettings;

  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [errorMessage, setErrorMessage] = useState('');

  const [lastVisibleCard, setLastVisibleCard] = useState(cardsPerPage);
  const totalCards = products.length;

  const scroll = -(lastVisibleCard - cardsPerPage) * (cardWidth + cardGap);
  const styles = {
    transform: `translateX(${scroll}px)`,
    transition: 'transform 0.5s',
  };

  useEffect(() => {
    fetchProducts()
      .then(productsFromServer => {
        const preparedProducts = filterProducts(
          filter,
          sortBy,
          productsFromServer,
        );

        setProducts(preparedProducts);
        setLoading(false);
      })
      .catch((error) => setErrorMessage(error));
  }, []);

  const handleCardsChange = (direction: Direction) => {
    let newCard;

    if (direction === Direction.NEXT) {
      newCard = lastVisibleCard + 1;

      if (newCard > totalCards) {
        newCard = totalCards;
      }
    } else {
      newCard = lastVisibleCard - 1;

      if (newCard < cardsPerPage) {
        newCard = cardsPerPage;
      }
    }

    setLastVisibleCard(newCard);
  };

  return (
    <div className="ProductsSlider">
      <div className="ProductsSlider__top">
        <h2 className="ProductsSlider__title">{title}</h2>

        <div className="ProductsSlider__buttons">
          <button
            type="button"
            className={`SliderButton SliderButton--${Direction.PREV}`}
            onClick={() => {
              handleCardsChange(Direction.PREV);
            }}
            disabled={lastVisibleCard === cardsPerPage}
          >
            {' '}
          </button>

          <button
            type="button"
            className={`SliderButton SliderButton--${Direction.NEXT}`}
            onClick={() => {
              handleCardsChange(Direction.NEXT);
            }}
            disabled={lastVisibleCard === totalCards}
          >
            {' '}
          </button>
        </div>
      </div>

      {errorMessage && <span>Something went wrong</span>}

      {!loading && (
        <div className="ProductsSlider__products-container">
          <div
            className="ProductsSlider__products"
            data-cy="cardsContainer"
          >
            {products.map(product => (
              <div key={product.id} style={styles}>
                <ProductCard product={product} />
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};
